.App {
  height: 100vh;
  font-size: 4vmin;
  text-shadow: 2px 3px 4px rgba(0, 0, 0, 0.25);
  font-family: 'Roboto', sans-serif;
}

.clock {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.clock-face {
  height: 60vmin;
  width: 60vmin;
  background: darkgrey;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 3vmin;
  border: 2.5vmin solid black;
  -webkit-box-shadow: 0px 6px 15px 5px #00000075, 0px 3px 15px 5px #00000075 inset;
  box-shadow: 0px 6px 15px 5px #00000075, 0px 3px 15px 5px #00000070 inset;
  font-family: 'Stint Ultra Condensed', cursive;
  font-size: 7vmin;
}

/* Center dot */
.clock-face::after { 
  content: '';
  position: absolute;
  height: 2vmin;
  width: 2vmin;
  background:black;
  border-radius: 50%;
  box-shadow: 0px 0px 15px 1px #00000050;
}

 .h, .m, .s {
  position: absolute;
  height: 50%;
  box-shadow: 0px 0px 15px 1px #00000050;
  border-radius: 0.2vmin;
}

 .h {
   width: 1.5vmin;
   height: 25vmin;
  transform: translate(0, -10vmin);
}

 .m {
  height: 27.5vmin;
  width: 0.75vmin;
  transform: translate(0, -11.25vmin);
 }

 .s {
  height: 30vmin;
  width: 0.5vmin;
  transform: translate(0, -12.5vmin);
}

 .num, .tick {
  position: absolute;
  height: 100%;
 }

 .num span, .tick span {
   position: absolute;
 }

 .tick span {
  transform: translateX(-50%);
  opacity: 0.5;
 }

.top {
  margin: 5px;
  position: absolute;
  top: 0;
  width: calc(100% - 10px);
}

.buttons {
  text-align: right;
  bottom: 0;
}

.heading {
  top: 0;
  overflow: hidden;
  float: left;
}

.heading h2, .heading p, .buttons {
  margin-top: 0;
  margin-bottom: 0;
}

.buttons label {
  font-size: 3.5vmin;
  font-weight: 500;
}

.heading input {
  font-size: 1vmin;
}